body {
  margin: 0;
  padding: 0;
  /*background-color: #dcdfe4;*/
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
	background-color: #282c34;
	height: 80px;
  	/*min-height: 100vh;*/
  	display: flex;
  	flex-direction: column;
  	align-items: center;
  	justify-content: center;
  	font-size: calc(10px + 2vmin);
  	color: white;
  	border-bottom: 2px solid grey;
  	border-bottom-width: thin;
}

.Header p {
  	font-size: 140%;
  	font-weight: bold;
}

.Header p span:nth-child(1) {
	color: red;
}
.Header p span:nth-child(2) {
	color: white;
}
.topnav {
	display: flex;
	/*flex-direction: row;*/
    flex-wrap: wrap;
	justify-content: space-between;	
	margin-bottom: 2%;
}

.topnav > * {
    min-width: 100px;
	padding: 12px 20px 12px 40px;
}


/* Small phones from 0px to 480px */
@media only screen and (max-width: 480px) {
	.topnav > * {
		padding: 6px 10px 6px 33px;
	}

	.topnav input[type=text] {
	    font-size: 16px;
	    background-position: 5px 5px;
	}
}
.SearchUser {
    
}

input[type=text] {
    width: 100%;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    background-color: white;
    background-image: url(data:image/png;base64,);
    background-position: 10px 10px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
}

/*input[type=text]:focus {
    width: 50%;
}*/

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    padding: 0;
}

.dropdown-content li {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    /*width: 100%;*/
    font-size: 100%;

    background-color: Transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

.dropdown-content li:hover {
    background-color: #f1f1f1
}

.dropdown .dropdown-content.opening {
    display: block;
    
}
.RatingItem {
	/*margin-bottom: 2%;*/
    word-break: break-word;   
    word-wrap: break-word;   
    overflow-wrap: break-word;   

    margin: 0.5em;
    padding: 0.5em;
    /*width: 400px;*/
    cursor: pointer;
    background: transparent;
    padding: 1rem 1rem;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    color: #41403E;
    outline: none;
    box-shadow: 20px 38px 34px -26px hsla(0,0%,0%,.2);
    border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
    border: solid 2px #41403E;

    margin-bottom: 2%;
}

.RatingItem > * {
    margin-bottom: 2%;
}

.RatingItem .title {
    text-align: left;
    width: 100%;
    font-size: calc(10px + 2vmin);
    font-weight: bold;
    color: #CE8686;
}

.RatingItem:hover {
	background: rgb(240, 240, 240);
    box-shadow: 2px 8px 4px -6px hsla(0,0%,0%,.3);
}

.RatingItem .winner {
    /*height: 100px;*/
    display: flex;
    flex-direction: row;
    align-items: center;
}

.RatingItem .winner .winner-name-box {
    width: 50%;
    text-align: center;
}
.RatingItem .winner .winner-place-box,
.RatingItem .winner .winner-scores-box {
    display: flex;
    flex-direction: row;
    position: relative;
    min-height: 1px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
}
.RatingItem .winner .winner-place-box {
    width: 30%;
    justify-content: flex-start;
}
.RatingItem .winner .winner-scores-box {
    width: 20%;
    justify-content: flex-end;
}

.RatingItem .winner .winner-place-box img {
    max-width: 100%;
    height: auto;
}

.RatingItem .winner .winner-circle {
    width: 100px;
    height: 0;
    color: #1a1a1a;
    padding-bottom: calc(120px - 1.4rem);
    border-radius: 50%;
    border: .3rem solid #1a1a1a;
    position: relative;
    margin: 0 -5%;
}

.RatingItem .winner .winner-scores-box .winner-circle {
    border-radius: 10%;
    width: 70%;
    min-width: 100px;
    border: .1rem solid #1a1a1a;
}

.RatingItem .winner .winner-name-box span {   
    font-size: 3rem;
    font-weight: 700;
}

.RatingItem .winner .winner-place-box span,
.RatingItem .winner .winner-scores-box span {
    display: block;
    width: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    font-size: 3rem;
    font-weight: 700;
}

.RatingItem .winner .winner-scores-box span {
    font-size: 2rem;
    font-weight: 500;
}



/* Big tablets to 1400px (widths smaller than 1400px row) */
@media only screen and (max-width: 1400px) {
    .RatingItem .winner .winner-place-box {
        width: 40%;
    }
}

/* Big tablets to 1200px (widths smaller than 1200px row) */
@media only screen and (max-width: 1200px) {
    .Ratings {
        width: 90%;
    }
} 

/* Small tablets to big tablets from 768px to 1024px */
@media only screen and (max-width: 1024px) {
    .RatingItem .winner .winner-place-box img {
        /*width: auto;
        height: 90px;*/
    }

    .RatingItem .winner-circle {
        width: 80px;
        padding-bottom: calc(100px - 1.4rem);
        border: .2rem solid #1a1a1a;
    }

    .RatingItem .winner .winner-name-box span { 
        font-size: 2rem;
    }

    .RatingItem .winner .winner-place-box span,
    .RatingItem .winner .winner-scores-box span {
        font-size: 2rem;
    }
}

/* Small phones to small tablets from 481px to 768px */
@media only screen and (max-width: 768px) {
    .RatingItem .winner .winner-place-box img {
        width: auto;
        height: 80px;
    }

    .RatingItem .winner .winner-circle {
        width: 70px;
        padding-bottom: calc(90px - 1.4rem);
        border: .2rem solid #1a1a1a;
    }

    .RatingItem .winner .winner-name-box span { 
        font-size: 1.5rem;
    }

    .RatingItem .winner .winner-place-box span,
    .RatingItem .winner .winner-scores-box span {
        font-size: 1.5rem;
    }
    .RatingItem .winner-scores-description {
        font-size: 0.9rem;
    }
}

/* Small phones to small tablets from 481px to 667px */
@media only screen and (max-width: 667px) {
    .RatingItem .winner .winner-place-box img {
        width: auto;
        height: 50px;
    }

    .RatingItem .winner .winner-circle {
        width: 50px;
        padding-bottom: calc(70px - 1.4rem);
        border: .1rem solid #1a1a1a;
    }
    .RatingItem .winner-scores-description {
        font-size: 0.7rem;
    }
}

/* Small phones from 0px to 480px */
@media only screen and (max-width: 480px) {
    .RatingItem .title {
        font-size: 0.8rem;
    }

    .RatingItem .winner .winner-place-box img {
        width: auto;
        height: 30px;
    }

    .RatingItem .winner .winner-circle {
        width: 30px;
        padding-bottom: calc(50px - 1.4rem);
        border: .1rem solid #1a1a1a;
    }

    .RatingItem .winner .winner .winner-circle {
        /*min-width: 50px;*/
    }

    .RatingItem .winner .winner-scores-box {    
        width: 20%;
    }
    .RatingItem .winner .winner-name-box span { 
        font-size: 1rem;
    }

    .RatingItem .winner .winner-place-box span,
    .RatingItem .winner .winner-scores-box span {
        font-size: 1rem;
    }
    .RatingItem .winner-scores-description {
        font-size: 0.5rem;
    }
}
